import React from 'react'

const Artist = () => {
  return (
    <>
    
            <div className="padb-30">
            <div className="container-fluid  ">
            <div className="row">
                <div className="col-lg-12">
                    <div className="feature-all">
                        <div className="featured-content">
                        <h2>About the Artwork

                        </h2>
                        <p className="mt-2">One morning, when Gregor Samsa woke from troubled dreams, he found
                            himself transformed in his bed into a horrible vermin.
                            He lay on his armour-like back, and if he lifted his head a little he could
                            see his brown belly, slightly domed and divided by arches into stiff
                            sections. The bedding was hardly able to cover it and seemed ready to
                            slide off any moment.
                            His many legs, pitifully thin compared with the size of the rest of him,
                            waved about helplessly as he looked. "What's happened to me? " he
                            thought. It wasn't a dream. His room, a proper human room although a
                            little too small, lay peacefully between its four familiar walls.
                        </p>
                        </div>
                    </div>
                </div>

            </div>
            </div>
        </div>
        
        <div className="container-fluid  ">
           <hr/>
        </div>
        </>

  )
}

export default Artist